@import "../../global.scss";

.menu {
  width: 300px;
  height: 100vh;
  background-color: $mainColor;
  position: fixed;
  list-style-type: decimal;
  top: 0;
  right: -300px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 1s ease;



  &.active {
    right: 0;
  }
  ul{
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-style: 40px;
    font-weight: 300;
    color: white;
    width: 60%;
    list-style-type: circle;
   
    li{
      
      margin-bottom: 25px;
        a{
          font-style: inherit;
          color: inherit;
          text-decoration: none;
        }
        &:hover{
          font-weight: 500;
        }

    }
  }
}